.nav {
    width: 100%;
    height: 150px;
    top: 0;
    position: fixed;
    padding: 0 10%;
    display: flex;
    align-items: center;
    background-color: rgba(77, 77, 77, 0.72);
    box-shadow: 2px 6px 15px 10px rgba(0, 0, 0, 0.38);
    z-index: 99;
    animation: ease fromTop 2s;
}

.nav a,
.nav img {
    height: 95%;
}

.navTitle {
    align-items: baseline;
    margin-left: 20px;
    color: rgb(228, 206, 206);
}

.navTitlePpal {
    font-size: 55px;
    font-weight: 800;
}

.navSubtitle {
    font-size: 25px;
    margin-left: 5px;
    text-decoration: underline;
}

@keyframes fromTop {
    from {
        transform: translateY(-200px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}